const SearchBar = () => {
    const history = useHistory();
    const [places, setPlaces] = useState("");

    return (
        <SearchContainer>
            <SearchBoxContainer value={places} setValue={(newPlace) => setPlaces(newPlace)}></SearchBoxContainer>
            <SearchButton onClick={() => history.push({pathname: './calculate', data: places})}>Let's go</SearchButton>
        </SearchContainer>
    );
}
export default Home