import React from 'react'
import MapContainer from './MapContainer'


const Calculate = (props) => (
    <div>
        <p>CALCULATE PAGE </p>
        <MapContainer center={{lat: JSON.parse(props.location.data).geometry.location.lat, 
            lng: JSON.parse(props.location.data).geometry.location.lng}} isMarkerShown={true}></MapContainer>
        {JSON.parse(props.location.data).name}
        {console.log(props.location.data)}

    </div>
)
export default Calculate