import styled from 'styled-components'

export const Container = styled.div`
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    background: linear-gradient(0deg, rgba(255,255,255,0.8216328767835259) 0%, 
      rgba(255,255,255,0) 0%, 
      rgba(255,255,255,0.1017449216014531) 31%, 
      rgba(255,255,255,0) 62%, 
      rgba(255,255,255,0.09894380115327384) 100%
    ),url(https://i.imgur.com/FVGKqT8.jpg);
    background-size: cover;
`
export const Title = styled.h1`
    font-family: 'Lato', sans-serif;
    font-weight: 700pt;
    font-size: 75pt;
    margin: 10px;
    color: white;
    margin-left: 60px;
`
export const SearchContainer = styled.div`
    width: 40vw;
    height: 7vh;
    display:flex;
    justify-content: space-between;
    border-radius: 10px;
    margin-left: 60px;
`
export const SearchInput = styled.input`
    border-radius: 10px;
    width: 100%;
    height: 100%;
    border:none;
    padding: 0px 0px 0px 30px;
    background-image: url(https://img.icons8.com/all/500/search--v1.png);
    background-size: 15px;
    background-repeat: no-repeat;
    background-position: 10px 12px;
    font-family: 'Lato', sans-serif;
    font-weight:400pt;
    &:focus{
      outline:none;
    }
`
export const SearchButton = styled.button`
    border-radius: 10px;
    /* flex-grow: 1; */
    margin-left: 15px;
    width: 25%;
    color: white;
    border: none;
    background-color: black;
    outline:none;
    font-family: 'Lato', sans-serif;
    font-weight:400pt;
    &:focus{
      outline:none;
    }
    &:hover{
      cursor:pointer;
    }
`

export const NavBarContainer = styled.div`
  margin:0;
  padding: 0;
`

export const NavBarList = styled.ul`
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: transparent;
  position: fixed;
`

export const NavBarItem = styled.li`
  float: left;
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  font-family: 'Lato', sans-serif;
  font-weight:400pt;
  text-decoration: none;
  &:hover{
    color: lightgray;
  }
`